.tgl {
    display: none;
}
.tgl+.tgl-btn {
    outline: 0;
    display: block;
    width: 50px;
    height: 26px;
    position: relative;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    }
    .tgl+.tgl-btn:hover{
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.36);
    }
    .tgl+.tgl-btn:after, .tgl+.tgl-btn:before {
    position: relative;
    display: block;
    content: "";
    width: 20px;
    height: 20px;
    }
    
    .tgl+.tgl-btn:after {
    left: 1px;
    }
    
    .tgl+.tgl-btn:before {
    display: none;
    }
    
    .tgl:checked+.tgl-btn:after {
    left: calc(50% + 1px);
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
    }
    
    .tgl-light+.tgl-btn {
    background: #9E9E9E;
    border-radius: 2em;
    padding: 3px;
    transition: all .4s ease;
    }
    
    .tgl-light+.tgl-btn:after {
    border-radius: 50%;
    background: #fff;
    transition: all .2s ease;
    }
    
    .tgl-light:checked+.tgl-btn {
    background: #3c9ece;
    background: linear-gradient(45deg, #2f89f9, rgba(47, 137, 249, 0.7));
    }
