/* CheckBox Style */

.promize-checkbox {
    display: inline-block;
    position: relative;
    padding-left: 30px;
    margin-bottom: 10px;
    text-align: left;
    cursor: pointer;
    font-size: 14px;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.promize-checkbox>input {
    position: absolute;
    z-index: -1;
    opacity: 0;
}

.promize-checkbox>span {
    border-radius: 3px;
    background: none;
    position: absolute;
    top: 1px;
    left: 0;
    height: 20px;
    width: 20px;
    border: 2px solid #5d78ff;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.promize-checkbox>span:after {
    content: '';
    position: absolute;
    display: none;
    top: 50%;
    left: 50%;
    margin-left: -3px;
    margin-top: -7px;
    width: 4px;
    height: 10px;
    border: solid #5d78ff;
    border-width: 0 2px 2px 0/*rtl:ignore*/
    !important;
    -webkit-transform: rotate(45deg)/*rtl:ignore*/
    ;
    transform: rotate(45deg)/*rtl:ignore*/
    ;
}

.promize-checkbox>input:checked~span:after {
    display: block;
}

/* Radio button Style */

.promize-radio {
    display: inline-block;
    position: relative;
    padding-left: 22px;
    text-align: left;
    margin-bottom: 10px;
    cursor: pointer;
    font-size: 13px;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.promize-radio>input {
    position: absolute;
    z-index: -1;
    opacity: 0;
}

.promize-radio>.chk-span {
    background: none;
    position: absolute;
    top: -1px;
    left: 0;
    height: 18px;
    width: 18px;
    border-radius: 50% !important;
    background-color: #fff;
    border: 1px solid #A7ABC3;
    -webkit-transition: all 0.3s ease;
    transition: all 0.3s ease;
}

.promize-radio>.chk-span:after {
    content: '';
    position: absolute;
    display: none;
    top: 50%;
    left: 50%;
    margin-left: -3px;
    margin-top: -3px;
    width: 6px;
    height: 6px;
    transition: all 0.3s ease;
    background: #ffffff;
    border-radius: 100% !important;
}
.inputgroup .disabled {
    opacity: 0.5;
    pointer-events: none;
}
.inputgroup .disabled.checked {
    opacity: 1;
}
.promize-radio>input:checked~.chk-span
{
    background-color: #2f89f9;
    border-color: #2f89f9;
}
.promize-radio>input:checked~.chk-span:after {
    display: block;
}
